#root {
  background-image: url('../images/homeBg.png');
  background-repeat: repeat-y;
  background-position-x: center;
  background-size: contain;
}

.contact-page-container {
  min-height: calc(100vh - 145px);
  position: relative;
  transition: 1s;
  padding: 70px 20px 60px;
  display: flex;
  flex-direction: column;
}

.contact-form {
  display: flex;
  flex-direction: column;
  margin: 10px 5px;
  font-size: 1.1em;
  align-items: center;
}

.contact-form-label {
  margin: 8px 0;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.contact-form-label input {
  margin: 8px 0;
  padding: 5px 5px;
  border-radius: 10px;
}

.contact-form-label textarea {
  margin: 8px 0;
  padding: 5px 5px;
  min-height: 10vh;
  border-radius: 10px;
}

.contact-form-button {
  color: white;
  font-weight: 800;
  text-transform: uppercase;
  font-size: 1.15em;
  letter-spacing: 3px;
  margin: 8px 0;
  padding: 8px 5px;
  border: none;
  background-color: black;
}
