.projects-page-container {
  min-height: 100vh;
  background-image: url('../images/homeBg.png');
  background-repeat: repeat-y;
  background-position-x: center;
  background-size: contain;
  position: relative;
  transition: 1s;
  z-index: -1;
}

.projects-page-container main {
  transition: 1s;
  padding: 60px 20px 0;
  display: flex;
  flex-direction: column;
}
